/* Globals style */

html {
  height: 100%;
}
body {
  height: 100%;
}

.fondo-welcome {
  background-image: url('../images/fondowelcome.PNG');
  background-repeat: no-repeat;
  background-size: 99.9% 99.9%; 
  min-height: 99.9vh !important; 
}

.card-align{
  margin-top: 20vh !important;
}

.card-background{
  background-color: rgba(0, 0, 0, 0.7) !important; 
  color: rgba(255, 255, 255, 1) !important;
}

.Rectangle-1 {
  padding-left: .9rem;
  background-color: #ffd700;
}

.button-registrarse{
  background-color: rgba(255, 255, 255, 0.6) !important; 
  float: right;
  border-color: #000000 !important;
  color:#000 !important;
  border-radius: 25px !important;
}

.button-registrarse:hover{
  background-color: rgba(1, 1, 1, 0.7) !important; 
  float: right;
  border-color: #f5efef !important;
  color:#f5efef !important;
  border-radius: 25px !important;
}

.login-div {
  background-color: #fff !important;
  margin-top: 1rem !important;
  border-radius: 5rem;
  padding: 3rem;
}

.input-login{
  border-radius: 0 5rem 5rem 0 !important;
}

.font-radius{
  border-radius: 5rem 0 0 5rem !important;
}

.btn-login {
  border-radius: 10rem !important;
}
